<template>
  <div class="find">
    <div class="find-head">
      <h4>发现</h4>
    </div>
    <div style="margin-top: 2.5rem;">
      <div class="nav">
        <div class="item" v-for="(n,index) in navList">
          <router-link :to="n.name">
            <i class="mintui" :class="[n.icon]"></i>
            <div class="title">{{n.title}}</div>
          </router-link>
        </div>
      </div>
      <div class="cell">
        <mt-cell title="分类" icon="fenlei" is-link href="#/classify"></mt-cell>
      </div>
      <div class="cell">
        <mt-cell title="排行榜" icon="paixangbang" is-link href="#/rankingList"></mt-cell>
        <mt-cell title="精品专区" icon="jingpin" is-link></mt-cell>
        <mt-cell title="今日免费" icon="mianfei" is-link></mt-cell>
        <mt-cell title="新书专区" icon="xinshu" is-link></mt-cell>
        <mt-cell title="完本专区" icon="wanben" is-link></mt-cell>
      </div>
      <div class="cell">
        <mt-cell title="话题" icon="huati" value="" is-link></mt-cell>
        <mt-cell title="专栏" icon="lanmu" value="梦想自己成为主角" is-link></mt-cell>
        <mt-cell title="对话" icon="duihua" value="这里有不一样的东西" is-link></mt-cell>
      </div>
      <div class="cell">
        <mt-cell title="活动中心" icon="huodong" is-link>
          <span class="bdr">有福利</span>
        </mt-cell>
        <mt-cell title="游戏中心" icon="youxi" value="玩游戏得起点币" is-link></mt-cell>
      </div>
    </div>

  </div>
</template>
<script>
  export default {
    name:'find',
    data(){
      return {
        navList:[
          {name:'',title:'书单',icon:'mintui-shudan'},
          {name:'',title:'漫画',icon:'mintui-manhua'},
          {name:'',title:'听书',icon:'mintui-tingshu'},
          {name:'',title:'红包',icon:'mintui-hongbao'}
        ]
      }
    },
    methods:{
      goClick(path){
        this.$router.push(path);
      }
    }
  }
</script>
<style scoped>
  .bdr {
    border: 1px solid red;
    padding: 0.5rem 0.8rem;
    border-radius: 1rem;
  }
  .find {
    margin-bottom: 3.2rem;
    background-color: #ddd;
    overflow: hidden;
  }
  .find-head {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    width: 100%;
    height: 40px;
    background: #E10601;
    line-height: 39px;
    color: #fff;
}
.find-head h4{
  margin:0;
  float: left;
  padding-left:3rem;
}
.nav {
  display: flex;
  justify-content: space-around;
  padding:1.5rem 0;
  background-color: #fff;
  margin-bottom: 0.5rem;
}
.item {
  width: 5rem;
}
.item a {
  text-decoration: none;
  font-family: 宋体;
  color:#000;
}
.item i {
  color:red;
}
.item .title {
  line-height: 26px;
}
.cell {
  overflow: hidden;
  clear:both;
  text-align: left;
  margin-bottom: 0.5rem;
}

</style>


